import React, { useEffect, useState } from 'react'
import {
  ResponsiveContainer,
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
} from 'recharts'

interface DataChartProps {
  containerHeight?: number
  lineColor?: string
}

interface RawPoint {
  id: number
  device_id: number
  recorded_at: string
  temperature: string | number
}

interface DataPoint {
  time: string
  temperature: number
}

export const DataChart: React.FC<DataChartProps> = ({
  containerHeight = 200,
  lineColor = '#60A5FA',
}) => {
  const [data, setData] = useState<DataPoint[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true)
      try {
        const token = localStorage.getItem('accessToken') || ''
        const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:5103'
        const response = await fetch(
          `${apiUrl}/api/temperature-readings`,
          {
            headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${token}`,
            },
          }
        )
        if (!response.ok) throw new Error(`Status ${response.status}`)

        const json: RawPoint[] = await response.json()

        const formatted: DataPoint[] = json.map((d) => ({
          time: new Date(d.recorded_at).toLocaleTimeString([], {
            hour: '2-digit',
            minute: '2-digit',
            second: '2-digit',
          }),
          temperature:
            typeof d.temperature === 'string'
              ? parseFloat(d.temperature)
              : d.temperature,
        }))

        setData(formatted)
      } catch (e: any) {
        console.error('Error fetching data:', e)
        setError(e.message)
      } finally {
        setLoading(false)
      }
    }

    fetchData()
    const id = setInterval(fetchData, 10000)
    return () => clearInterval(id)
  }, [])

  if (loading) return <div>Loading chart…</div>
  if (error) return <div className="text-red-500">Error: {error}</div>

  return (
    <ResponsiveContainer width="100%" height={containerHeight}>
      <LineChart data={data}>
        <CartesianGrid strokeDasharray="3 3" stroke="#ffffff40" />
        <XAxis dataKey="time" stroke="#000" tick={{ fill: '#000' }} />
        <YAxis domain={['auto', 'auto']} unit="°C" stroke="#000" tick={{ fill: '#000' }} />
        <Tooltip contentStyle={{ backgroundColor: '#fff' }} itemStyle={{ color: lineColor }} />
        <Line type="monotone" dataKey="temperature" stroke={lineColor} dot={false} strokeWidth={2} />
      </LineChart>
    </ResponsiveContainer>
  )
}